import React, { useEffect, useState } from 'react'
import { AiOutlineInstagram, AiOutlineGithub, AiOutlineLinkedin } from 'react-icons/ai';

function Navbar() {
    const [scroll, setScroll] = useState(false);
        useEffect(() => {
        window.addEventListener("scroll", () => {
            setScroll(window.scrollY > 10);
        });
 }, []);
  return (
    <div className={scroll ? "w-full fixed border-slate-500 top-0" : "w-full fixed border-slate-500 top-0 border-b-[1px]"}>
        <div className='max-w-[1240px] h-[70px] flex justify-between mx-auto px-4 items-center text-slate-500'>
        <div className='text-2xl sm:text-3xl font-extrabold'>Daka</div>
            <div>
                <ul className='flex md:hidden'>
                    <li><a href="https://www.instagram.com/kang_piscokk/" target="_blank"><AiOutlineInstagram size={25}/></a></li>
                    <li><a href="https://github.com/wamandaka" target="_blank"><AiOutlineGithub size={25}/></a></li>
                    <li><a href="https://www.linkedin.com/in/waman-daka-2498a11ab/" target="_blank"><AiOutlineLinkedin size={25}/></a></li>
                </ul>
                <ul className='md:flex hidden'>
                    <li className='px-4'><a href="https://www.instagram.com/kang_piscokk/" target="_blank">Instagram</a></li>
                    <li className='text-white cursor-default'>/</li>
                    <li className='px-4'><a href="https://github.com/wamandaka" target="_blank">Github</a></li>
                    <li className='text-white cursor-default'>/</li>
                    <li className='px-4'><a href="https://www.linkedin.com/in/waman-daka-2498a11ab/" target="_blank">LinkedIn</a></li>
                </ul>
            </div>
        </div>
    </div>
  )
}

export default Navbar